import { Description, Header, Container, Details, Link, Wrapper } from "./styled";

export const Tile = ({ description, url, gitUrl, name }) => {
    return (
        <>
            {/* <Wrapper> */}
            <Container>
                <Header>{name && name}</Header>
                <Description>
                    {description && description}
                </Description>
                <Details>
                    Demo: <Link href={url && url}>{url & url}</Link>
                </Details>
                <Details>
                    Code: <Link href={gitUrl && gitUrl}>{gitUrl && gitUrl}</Link>
                </Details>
            </Container>
            {/* </Wrapper> */}
        </>
    )
};